'use client';
import { Flex, Image, Box } from '@chakra-ui/react';
import { keyframes } from '@emotion/react';
import React from 'react';

// Define the keyframes outside the component
const slideAnimation = keyframes`
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(-50%);
  }
`;

function Partners() {
  const sources = [
    '/icons/uzb_airways.svg',
    '/icons/turk_airways.svg',
    '/icons/qatar_airways.svg',
    '/icons/emirate_airways.svg',
    '/icons/lutfhansa_airways.svg',
    '/icons/american_airlines.svg',
    '/icons/somon_air.svg',
    '/icons/air_astana.svg',
    '/icons/etihad_airways.svg',
  ];

  // Duplicate the sources array to create a seamless loop
  const duplicatedSources = [...sources, ...sources];

  return (
    <Box overflow='hidden' width='100%' mt={11}>
      <Flex
        className='carousel'
        animation={`${slideAnimation} 20s linear infinite`}
        whiteSpace='nowrap'
        width='max-content'
        gap={7}
      >
        {duplicatedSources.map((src, index) => (
          <Image
            key={index}
            src={src}
            alt={`partner-logo-${index}`}
            height='50px' // Adjust height as needed
            display='inline-block'
          />
        ))}
      </Flex>
    </Box>
  );
}

export default Partners;
